








import Vue from "vue";
import { Localizer } from "../localization/localizer";
export default Vue.extend({
  computed: {
    gameName() {
      return Localizer.get("gameName");
    },
    viewSettings() {
      return this.$store.state.viewSettings;
    }
  },
  methods: {
    toggleSettings() {
      this.$store.state.viewSettings = !this.$store.state.viewSettings;
    }
  }
});
